:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.newMessage{display:flex;flex-direction:column;align-items:center;margin-top:auto}.newMessage__bottom{margin-top:20px;width:100%;padding:0 40px;display:flex;justify-content:space-between;align-items:center}.newMessage__bottom i{color:#a4aebe;cursor:pointer}.newMessage__btn{display:inline-block;font-weight:700;text-align:center;vertical-align:middle;user-select:none;background-color:transparent;border:1px solid transparent;padding:.8rem 2.1rem;font-size:.688rem;line-height:1.5;border-radius:0.3rem;transition:all .15s ease-in-out;cursor:pointer;color:#fff;background-color:var(--secondary-color);cursor:pointer}.newMessage__btn:hover{opacity:0.9}
