:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.container{margin:0 auto;display:flex;flex-direction:column;background:#fff;border-radius:5px;min-width:50%}.intro{padding:25px;border:1px solid #e6ecf5;font-size:.875rem;color:var(--active-text-color);font-weight:bold}.form{display:flex;flex-direction:column;padding:35px 25px;width:100%}.loginInput,.loginPassword,.checkboxContainer{margin-bottom:15px}.checkboxContainer{display:flex;align-items:center}.checkboxContainer span{font-size:0.75rem;margin-left:10px;color:var(--primary-text-color)}.inputContainer input{width:100%;padding:10px 7px;border:1px solid #e6ecf5;color:var(--primary-text-color)}.loginButton{display:inline-block;font-weight:700;text-align:center;vertical-align:middle;user-select:none;background-color:transparent;border:1px solid transparent;padding:.8rem 2.1rem;font-size:.688rem;line-height:1.5;border-radius:0.3rem;transition:all .15s ease-in-out;cursor:pointer;color:#fff;background-color:var(--secondary-color);width:100%}.loginButton:hover{opacity:0.9}
